import React from "react";
import Pure from "./Pure";
import "./Messages.scss";
import { Link, Navigate } from "react-router-dom";
import client from "./CLASSES/client";
import since from "./Functions/since";
import { Button } from "react-bootstrap";
import ImageMap from "./Images";
import validators from "./Functions/validation";
import { SnackBar, openSnackBar } from "./Interfaces/SnackBar";
import getParam from "./Functions/getParam";

class Messages extends React.PureComponent {
    decide() {
        let p = this.props.display;
        if (p === "view") return <View />;
        if (p === "send") return <Send />;
        if (p === "sent") return <Sent />;
        if (p === "note") return <Note />;
        return <Inbox />;
    }
    render() {
        return <div id="Messages"><SnackBar register="snackbar" />{this.decide()}</div>;
    }
}

class Inbox extends Pure {
    state = {
        CurrentPage: 1,
        PagesLength: 1,
        Content: <></>
    }
    renderPages(page = 1) {
        client.viewInbox(page).then(inbox => {
            if (!this.componentIsReady) return;
            let messages = inbox.messages;
            if (messages.length === 0) {
                this.setState({ Content: <tr><td colSpan={4} className="empty">فارغ</td></tr> });
            } else {
                let content = [];
                inbox.messages.reverse().forEach((message, index) => {
                    content.push(<tr key={[message.messageId, index].toString()}>
                        <td><input type="checkbox" /></td>
                        <td className="subject"><Link to={"/Messages/view?id=" + message.messageId}>{message.subject || "بدون موضوع"}</Link>{message.seen ? "" : "(لم تقرأ)"}</td>
                        <td><Link to={"/Profile?id=" + message.senderId}>{message.senderName}</Link></td>
                        <td title={new Date(message.sent).toLocaleString("ar")}>منذ {since(message.sent)}</td>
                    </tr>);
                });
                this.updateState({ CurrentPage: inbox.currentPage, PagesLength: inbox.pages, Content: content });
            }
        });
    }
    onReady() {
        this.renderPages(this.state.CurrentPage);
    }
    render() {
        return <>
            <h4>الرسائل الواردة</h4>
            <Menu highlight={1} />
            <table className="table_collapsed">
                <thead>
                    <tr>
                        <th><input type="checkbox" /></th>
                        <th className="subject">الموضوع</th>
                        <th>المرسل</th>
                        <th>تاريخها</th>
                    </tr>
                </thead>
                <tbody>{this.state.Content}</tbody>
                <tfoot>
                    <tr>
                        <td colSpan={4}>
                        </td>
                    </tr>
                </tfoot>
            </table>
        </>
    }
}

class View extends Pure {
    state = {
        title: "قراءة الرسالة",
        button: "كتابة رد",

        username: "",
        subject: "",
        content: "",

        answer: false,
        ready: false,
        notFound: false,
        redirect: false
    }

    data = {
        username: "",
        subject: "",
        content: ""
    }

    ref1 = React.createRef();
    ref2 = React.createRef();
    ref3 = React.createRef();
    val = i => this['ref' + i].current.value;

    onReady() {
        client.viewMessage({ id: getParam("id", 0, true) }).then(message => {
            if (!this.componentIsReady) return;
            if (message.messageId > 0)
                this.setState({
                    username: message.senderName,
                    subject: message.subject,
                    content: message.content,
                    ready: true,
                    found: true
                });
            else {
                this.setState({ ready: true, notFound: true });
                openSnackBar("لا يمكن العثور على الرسالة");
            }
        });
    }
    actionHandler() {
        return e => {
            e.preventDefault();
            if (this.answer()) {
                this.data.username = this.val(1);
                this.data.subject = this.val(2);
                this.data.content = this.val(3);
                sendMessage(this.data, () => this.updateState({ redirect: true }));
            }
        }
    }
    answer() {
        if (this.state.answer)
            return true;
        let subject = this.state.subject;
        if (subject.startsWith("رد."))
            subject = subject.replace(/رد\.(\d+):/, (x, y) => { return "رد." + (Number(y) + 1) + ":" });
        else
            subject = "رد.1: " + subject;
        let content = "\n_________________________________\nمن " + this.state.username + ":\n\n" + this.state.content;
        this.ref2.current.value = subject;
        let textarea = this.ref3.current;
        textarea.value = content;
        textarea.focus();
        textarea.setSelectionRange(0, 0);

        this.updateState({
            title: "كتابة الرد",
            button: "إرسال الرد",
            answer: true,
        });
        return false;
    }
    render() {
        if (!this.state.ready)
            return <></>;
        else if (this.state.redirect)
            return <Navigate to="/Messages/sent" />;
        else if (this.state.notFound)
            return <><h4>{this.state.title}</h4><Menu highlight={3} /></>;
        else
            return <>
                <h4>{this.state.title}</h4>
                <Menu highlight={3} />
                <img alt="note_header" className="note_header" src={ImageMap.text.note_header} />
                <form className="send" onSubmit={this.actionHandler()}>
                    <div className="header">
                        <table>
                            <tbody>
                                <tr>
                                    <td className="green">المستلم</td>
                                    <td><input value={this.state.username} ref={this.ref1} readOnly={true} className="underline" type="text" /></td>
                                </tr>
                                <tr>
                                    <td className="green">الموضوع</td>
                                    <td><input defaultValue={this.state.subject} ref={this.ref2} readOnly={!this.state.answer} className="underline" type="text" /></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="body">
                        <textarea defaultValue={this.state.content} ref={this.ref3} readOnly={!this.state.answer} className="underline"></textarea>
                    </div>
                    <div className="footer">
                        <Button type="submit" variant={!this.state.answer ? "secondary" : "dark"} size="sm">{this.state.button}</Button>
                    </div>
                </form>
            </>
    }
}

class Send extends Pure {
    data = {
        username: "",
        subject: "",
        content: ""
    }
    state = {
        redirect: false
    }
    sendHandler() {
        return e => {
            e.preventDefault();
            sendMessage(this.data, () => this.updateState({ redirect: true }));
        }
    }
    render() {
        if (this.state.redirect)
            return <Navigate to="/Messages/sent" />;
        else
            return <>
                <h4>كتابة رسالة</h4>
                <Menu highlight={3} />
                <img alt="note_header" className="note_header" src={ImageMap.text.note_header} />
                <form className="send" onSubmit={this.sendHandler()}>
                    <div className="header">
                        <table>
                            <tbody>
                                <tr>
                                    <td className="green">المستلم</td>
                                    <td><input defaultValue={getParam("name")} onChange={e => { this.data.username = e.currentTarget.value; }} className="underline" type="text" /></td>
                                </tr>
                                <tr>
                                    <td className="green">الموضوع</td>
                                    <td><input onChange={e => { this.data.subject = e.currentTarget.value; }} className="underline" type="text" /></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="body">
                        <textarea onChange={e => { this.data.content = e.currentTarget.value; }} className="underline"></textarea>
                    </div>
                    <div className="footer">
                        <Button type="submit" variant="secondary" size="sm">إرسال</Button>
                    </div>
                </form>
            </>
    }
}

class Sent extends Pure {
    state = {
        CurrentPage: 1,
        PagesLength: 1,
        Content: <></>
    }
    renderPages(page = 1) {
        client.viewSentMessages(page).then(sent => {
            if (!this.componentIsReady) return;
            let messages = sent.messages;
            if (messages.length === 0) {
                this.setState({ Content: <tr><td colSpan={4} className="empty">فارغ</td></tr> });
            } else {
                let content = [];
                sent.messages.reverse().forEach((message, index) => {
                    content.push(<tr key={[message.messageId, index].toString()}>
                        <td><input type="checkbox" /></td>
                        <td className="subject"><Link to={"/Messages/view?id=" + message.messageId}>{message.subject}</Link>{message.seen ? "" : "(لم تقرأ)"}</td>
                        <td><Link to={"/Profile?id=" + message.receiverId}>{message.receiverName}</Link></td>
                        <td title={new Date(message.sent).toLocaleString("ar")}>منذ {since(message.sent)}</td>
                    </tr>);
                });
                this.updateState({ CurrentPage: sent.currentPage, PagesLength: sent.pages, Content: content });
            }
        });
    }
    onReady() {
        this.renderPages(this.state.CurrentPage);
    }
    render() {
        return <>
            <h4>الرسائل المرسلة</h4>
            <Menu highlight={2} />
            <table className="table_collapsed">
                <thead>
                    <tr>
                        <th><input type="checkbox" /></th>
                        <th className="subject">الموضوع</th>
                        <th>المستلم</th>
                        <th>تاريخها</th>
                    </tr>
                </thead>
                <tbody>{this.state.Content}</tbody>
                <tfoot>
                    <tr>
                        <td colSpan={4}>
                        </td>
                    </tr>
                </tfoot>
            </table>
        </>
    }
}

class Note extends Pure {
    state = {
        plus: false,
        ready: false,
        content: "",
    }
    note = React.createRef();

    onReady() {
        client.viewPlusNote().then(note => {
            if (!this.componentIsReady) return;
            if (note.error) {
                openSnackBar(note.reason);
                this.setState({ ready: true });
            } else {
                this.setState({ content: note.content, ready: true, plus: true });
            }
        });
    }
    save() {
        return e => {
            e.preventDefault();
            let noteValue = this.note.current.value;
            let content = validators.safeText(noteValue, 0, 1024);
            if (!content.valid)
                openSnackBar(content.result);
            else
                client.savePlusNote({ content: noteValue }).then(response => {
                    if (response.error)
                        openSnackBar(response.reason);
                    else
                        openSnackBar("تم الحفظ");
                });
        }
    }
    render() {
        if (!this.state.ready)
            return <></>;
        else if (!this.state.plus)
            return <>
                <h4>دفتر الملاحظات</h4>
                <Menu highlight={4} />
                <div className="plus-needed">يجب تفعيل حساب بلاس لإستخدام الدفتر</div>
            </>;
        else
            return <>
                <h4>دفتر الملاحظات</h4>
                <Menu highlight={4} />
                <img alt="note_header" className="note_header" src={ImageMap.text.note_header} />
                <form className="note" onSubmit={this.save()}>
                    <div className="body">
                        <textarea ref={this.note} defaultValue={this.state.content} className="underline"></textarea>
                    </div>
                    <div className="footer">
                        <Button type="submit" variant="secondary" size="sm">حفظ</Button>
                    </div>
                </form>
            </>
    }
}

class Menu extends React.PureComponent {
    highlight(n = 0) {
        return this.props.highlight === n ? { className: "highlight" } : {};
    }
    render() {
        return <div className="player-controls">
            <Link to="/Messages" {...this.highlight(1)}>الوارد</Link>&nbsp;|&nbsp;
            <Link to="/Messages/sent" {...this.highlight(2)}>الصادر</Link>&nbsp;|&nbsp;
            <Link to="/Messages/send" {...this.highlight(3)}>كتابة رسالة</Link>&nbsp;|&nbsp;
            <Link to="/Messages/note" {...this.highlight(4)}>دفتر الملاحظات</Link>
        </div>
    }
}

function sendMessage(data = {}, onSend = () => { }) {
    let username = validators.safeUsername(data.username);
    let subject = validators.safeText(data.subject, 0, 32);
    let content = validators.safeText(data.content, 0, 1024);
    if (!username.valid) openSnackBar(username.result);
    else if (!subject.valid) openSnackBar("موضوع الرسالة: " + subject.result);
    else if (!content.valid) openSnackBar("نص الرسالة: " + content.result);
    else {
        client.sendMessage(data).then(result => {
            if (result.error)
                openSnackBar(result.e_username + result.e_subject + result.e_content);
            else if (result.done)
                onSend();
            else
                openSnackBar("يوجد خطأ غير معروف, يرجى المحاولة لاحقاً او قم بتحديث الصفحة.");
        });
    }
}

export default Messages;