import React from 'react';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import "./SnackBar.css";
import Pure from '../Pure';
import sharedMemory from '../CLASSES/SharedMemory';

/** @augments {Pure<{ vertical: String, horizontal: String, duration: Number }>} */
class SnackBar extends Pure {
    state = { visible: false }
    message = "";
    open(msg = "") {
        this.message = msg;
        this.updateState({ visible: true });
    }
    close() {
        this.updateState({ visible: false });
    }

    render() {
        return <Snackbar
            anchorOrigin={{ vertical: this.props.vertical || "bottom", horizontal: this.props.horizontal || "center" }}
            open={this.state.visible}
            autoHideDuration={6000}
            message={this.message}
            onClose={() => this.close()}
            className="toast-message"
            action={
                <IconButton size="small" aria-label="close" color="inherit" onClick={() => this.close()}>
                    <CloseIcon fontSize="small" />
                </IconButton>
            }
        />
    }
}

let openSnackBar = (text = "") => { sharedMemory.safe("snackbar", sb => sb.open(text)) };

export { SnackBar, openSnackBar };